.header {
  position: absolute;
  top: 3px;
  height: 80px;
  width: 100%;
  background: transparent;
  z-index: 100;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
}

.header .is-menu-fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

