  margin-right: 45px;
  letter-spacing: .5px;
}

.header .heeder__nav .nav .nav__item a {
  color: #fff;
  border: 1px solid transparent;
  text-decoration: none;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -ms-transition: all 0.5s;
  transition: all 0.5s;
}

.header .heeder__nav .nav .nav__item a:hover {
  opacity: .5;
}

