  -webkit-transform: translate(250%, 0);
  transform: translate(250%, 0);
  background: linear-gradient(to bottom, #5dabfa, #147de7);
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  -webkit-transition: all 0.5s;
