  vertical-align: middle;
  width: 0;
  height: 100%;
}

.footer__block .footer__down .socials > * {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 3px solid #fff;
  vertical-align: middle;
  display: inline-block;
  margin-right: 17px;
  -moz-transition: opacity 0.5s;
  -o-transition: opacity 0.5s;
  -webkit-transition: opacity 0.5s;
  -ms-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.footer__block .footer__down .socials > *:hover {
  opacity: .5;
}

.footer__block .footer__down .socials svg {
  display: block;
  width: 2.3em;
  height: 2.3em;
}

.modal {
  display: none;
  position: absolute;
  z-index: 100;
  background: #383838;
  left: 50.5%;
  bottom: 0;
  width: 868px;
  height: 473px;
  transform: translate(-50%, 0);
  overflow: hidden;
}

.modal.is-open {
  display: block;
}

.modal.is-open .modal__inner {
  opacity: 1;
  transform: none;
}

.modal__inner {
  display: table;
  width: 100%;
  height: 100%;
  transform: translate(0, 100%);
  opacity: 0;
  transition: 0.5s ease;
  transition-property: opacity, transform;
}

.modal__body {
  position: relative;
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  padding: 20px;
  width: 100%;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
  overflow: hidden;
}

.modal__close {
  background: none;
  position: absolute;
  z-index: 2;
  top: 0;
  right: 0;
  padding: 20px;
  font-size: 00px;
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
}

.modal__close span {
  position: absolute;
  background-color: #fff;
  box-shadow: none;
  display: block;
  height: 3px;
  margin-top: 4px;
  position: relative;
  width: 25px;
  transform: rotate(45deg);
  top: -5px;
  -moz-transition: opacity 0.5s;
  -o-transition: opacity 0.5s;
  -webkit-transition: opacity 0.5s;
  -ms-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.modal__close span:after {
  content: "";
  position: absolute;
  transform: rotate(-90deg);
  top: -9px;
  background-color: #fff;
  box-shadow: none;
  display: block;
  height: 3px;
  margin-top: 4px;
  position: relative;
  top: -4px;
  width: 25px;
  -moz-transition: opacity 0.5s;
  -o-transition: opacity 0.5s;
  -webkit-transition: opacity 0.5s;
  -ms-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.modal__close span:after:hover {
  opacity: .7;
}

.modal__close span:hover {
  opacity: .7;
}

.modal__player {
  padding: 40px 0 0;
}
