body {
	font-family: 'OpenSans', Arial, sans-serif;
	font-size: 15px;
	@include font-smoothing;
	background: grey;
	color: #FFF; }
//variables
* {
	@include box; }

$orange: #ee931e;
$blue-light: #5dabfa;
$blue-dark: #147de7;
$menu-back: #051523;
$red: #fcead1;
$orange-light: #ffe084;
$orange-dark: #ee931e;
$footer: #383838;
$color-dark: #383838;




%in {
	min-width: 1170px;
	width: percentage(1170/1440);
	margin: 0 auto;
	max-width: 1440px; }


.out {
	overflow: hidden;
	color: black; }

h2 {
	line-height: 1.1;
	@include f(l,36); }

h3 {
	@include f(l,18,1); }


