.svg-icon {
	display: inline-block;
	svg {
		display: block;
		width: 100%;
		height: 100%; } }


.btn {
	display: inline-block;
	background: none;
	box-sizing: border-box;
	text-transform: uppercase;
	text-align: center;
	position: relative;
	z-index: 2;
	@include middle;
	background: transparent;
	margin-right: 0;
	text-decoration: none;
	overflow: hidden;
	border-radius: 100px;
	@include f(semi, 16);
	@include tr(opacity .5s);

	.btn__inner {
		font-size: 0;
		line-height: 0px;
		border: 3px solid transparent;
		display: inline-block;
		vertical-align: middle;
		overflow: hidden;
		& > * {
			@include middle; } }
	&:hover {
		opacity: .7; } }

.btn__text {
	position: relative;
	z-index: 1; }


