@include font("OpenSans", OpenSans-Regular);
@include font("OpenSans-Semibold", OpenSans-Semibold);
@include font("OpenSans-Light", OpenSans-Light);

@mixin f($name, $font-size: null, $letter-spacing: null) {
  $font-family: null;
  @if $name == "s" {
    $font-family: "OpenSans"; }
  @if $name == "semi" {
    $font-family: "OpenSans-Semibold"; }
  @if $name == "l" {
    $font-family: "OpenSans-Light"; }
  font-family: $font-family;
  font-weight: normal;
  @if $font-size != null {
    font-size: $font-size + px; }
  @if $letter-spacing != null {
    letter-spacing: ($letter-spacing / 1000) + em; } }
